import './adminUserModal.css';
import axios from "axios";
import {useEffect, useState} from "react";

const AdminUserModal = ({ user, onClose }) => {
    const [reservesToAdd, setReservesToAdd] = useState(0);
    const [orders, setOrders] = useState(null);
    const [loading, setLoading] = useState(true); //  

    useEffect(() => {
        axios.get(`/orders/${user.memberUid}`)
            .then(response => {
                setOrders(response.data); //    
                setLoading(false); //   
            })
            .catch(error => {
                setLoading(false); //      
                alert(error.message);
            });
    }, [user.memberUid]); // user.memberUid    

    const handleAddReserves = () => {
        const reserves = parseInt(reservesToAdd, 10);

        if (!isNaN(reserves) && reserves > 0) {
            axios.put(`/api/user/reservesAdd/${user.memberUid}`, { memberReserves : reserves })
                .then(response => {
                    alert('  .');
                    onClose(); //       .
                })
                .catch(error => {
                    alert('    : ' + error.message);
                });
        } else {
            alert('  .');
        }
    };

    const updateUserRole = () => {
        const roleSelect = document.getElementById("roleSelect");
        const selectedRole = roleSelect.value;

        axios.post(`/api/user/upsetRole?memberUid=${user.memberUid}&role=${selectedRole}`)
            .then(response => {
                alert('  .');
                onClose(); //       .
            })
            .catch(error => {
                alert('    : ' + error.message);
            });
    };

    return (
        <>
            <div className="modal">
                <div className="modal-content">
                    <div className="modal-header">
                        <span className="header-name"></span>
                        <span className="close" onClick={onClose}>&times;</span>
                    </div>
                    <div className="modal-body">
                        <div className="modal-body">
                            <h2>{user.memberName}</h2>
                            <p> : {user.memberEmail}</p>
                            <p> : {user.memberGender}</p>
                            <p> : {user.memberRole}</p>
                            <div className="member-role-upset">
                                <select className="member-role-select" id="roleSelect">
                                    <option value="USER"></option>
                                    <option value="ADMIN"></option>
                                    <option value="MARKETER"></option>
                                </select>
                                <button className="edit-button" onClick={updateUserRole}></button>
                            </div>
                            <p> : {user.memberDate}</p>
                            <p> : {user.memberReserves}</p>
                            <p> : {user.loginDate}</p>
                        </div>
                        <div className="modal-body-side">
                            <h2> </h2>
                            <div className="modal-point-add">
                                <input
                                    className="modal-input"
                                    type="number"
                                    placeholder=" "
                                    value={reservesToAdd}
                                    onChange={e => setReservesToAdd(e.target.value)}
                                ></input>
                            &nbsp;
                            <button className="edit-button" onClick={handleAddReserves}> </button>
                            </div>
                        </div>
                        <div className="modal-body-right">
                            <h2></h2>
                            <div>
                                {loading ? (
                                    <div>Loading...</div>
                                ) : !orders || orders.length === 0 ? (
                                    <div> .</div>
                                ) : (
                                    <div className="modal-order-info">
                                        <table>
                                            <thead>
                                            <tr>
                                                <th> ID</th>
                                                <th> </th>
                                                <th> </th>
                                                <th> </th>
                                                <th> ID</th>
                                            </tr>
                                            </thead>
                                            <tbody>
                                            {orders.map((order) => (
                                                <tr key={order.orderId}>
                                                    <td>{order.orderId}</td>
                                                    <td>{order.orderDate}</td>
                                                    <td>{order.totalPrice}</td>
                                                    <td>{order.paymentInformation}</td>
                                                    <td>
                                                        {order.goodsId.map((id, index) => (
                                                            <span
                                                                key={id}>{id}{index < order.goodsId.length - 1 ? ', ' : ''}</span>
                                                        ))}
                                                    </td>
                                                </tr>
                                            ))}
                                            </tbody>
                                        </table>
                                    </div>
                                )}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
        ;
}

export default AdminUserModal;